import "./hub.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import SideNav from "../../../components/navigators/endzone-side-nav/side-nav";
import ToolFolder from "../../../components/tools/endzone-tool-folder/tool-folder";

import StorageIcon from '@mui/icons-material/Storage';
import TableViewIcon from '@mui/icons-material/TableView';
import PostAddIcon from '@mui/icons-material/PostAdd';
import SportsFootballIcon from '@mui/icons-material/SportsFootball';
import UnfoldMoreDoubleIcon from '@mui/icons-material/UnfoldMoreDouble';
import NewspaperIcon from '@mui/icons-material/Newspaper';
import SchoolIcon from '@mui/icons-material/School';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import HeadsetMicIcon from '@mui/icons-material/HeadsetMic';
import ScienceIcon from '@mui/icons-material/Science';
import AssessmentIcon from '@mui/icons-material/Assessment';

function Hub()
{
    var search_data = [
        {name: "Home", tags: ["Home", "Main Menu", "Endzone"], link: "/"},
        {name: "Manage Data", tags: ["Data", "Manage", "Management"], link: "/endzone/data/manage"},
        {name: "View Data", tags: ["View", "Data"], link: "/endzone/data/view"},
        {name: "Create Data", tags: ["Create", "Data"], link: "/endzone/data/game"},
        {name: "Formations", tags: ["Coaches", "Corner", "Formation", "Playbook"], link: "/endzone/coaches-corner/formation"}
    ];

    return (
        <div className="hubPage">
            <EndzoneNav links={[]}></EndzoneNav>
            <div className="hub-body">
                <SideNav search_data = {search_data} links = {search_data}>
                    
                </SideNav>
                <div className="hub-menu">
                    <div className="hub-left">
                        <ToolFolder folder_name = "Data Management" link = "/data/hub"
                            tools = {
                                [
                                    {"tool_name": "Manage Data", "tool_text": "Create, load and manage your squad's games", "tool_link": "/endzone/data/manage", "icon": (<StorageIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></StorageIcon>)},
                                    {"tool_name": "Create Data", "tool_text": "Create new data with manual or Ai-Assisted entry", "tool_link": "/endzone/data/game", "icon": (<PostAddIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></PostAddIcon>)},
                                    {"tool_name": "View More", "tool_text": "Click to see what the data hub has to offer.", "tool_link": "/endzone/data/hub", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                ]
                            }>
                        </ToolFolder>
                        
                        <ToolFolder folder_name = "Coaches Corner" link = "/endzone/coaches/hub"
                            tools = {
                                [
                                    {"tool_name": "Formations", "tool_text": "Manage your team's formations", "tool_link": "endzone/coaches-corner/formation", "icon": (<SportsFootballIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></SportsFootballIcon>)},
                                    {"tool_name": "View More", "tool_text": "Click to see what the coaches corner has to offer.", "tool_link": "endzone/coaches-corner/hub", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                ]
                            }>
                        </ToolFolder>

                        <ToolFolder folder_name = "Endzone Clinic" link = "/endzone/learn/hub"
                            tools = {
                                        [
                                            {"tool_name": "News", "tool_text": "See what's new and upcoming for Endzone.", "tool_link": "/", "icon": (<NewspaperIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></NewspaperIcon>)},
                                            {"tool_name": "Video", "tool_text": "Watch tutorials on how to use our platform", "tool_link": "/", "icon": (<SchoolIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></SchoolIcon>)},
                                            {"tool_name": "Docs", "tool_text": "Read through walk-through's, and examples of Endzone's tools", "tool_link": "/", "icon": (<MenuBookIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></MenuBookIcon>)}
                                        ]
                                    }>
                        </ToolFolder>
                    </div>
                    <div className="hub-right">
                        <ToolFolder folder_name = "Pre-Game Tools" link = "/endzone/pregame/hub"
                            tools = {
                                        [
                                            {"tool_name": "Scouting", "tool_text": "Create a pre-game report for an upcoming opponent.", "tool_link": "/", "icon": (<ScienceIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></ScienceIcon>)},
                                            {"tool_name": "View More", "tool_text": "Click to view all pre-game tools", "tool_link": "/", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                        ]
                                    }>
                        </ToolFolder>
                        <ToolFolder folder_name = "In-Game Tools" link = "/endzone/ingame/hub"
                            tools = {
                                    [
                                        {"tool_name": "Coach Mike", "tool_text": "Predict plays in real time with Coach Mike", "tool_link": "/", "icon": (<HeadsetMicIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></HeadsetMicIcon>)},
                                        {"tool_name": "View More", "tool_text": "Click to view all in-game tools", "tool_link": "/", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                    ]
                                }>
                        </ToolFolder>

                        <ToolFolder folder_name = "Post-Game Tools" link = "endzone/postgame/hub"
                            tools = {
                                [
                                    {"tool_name": "Recap Report", "tool_text": "Just got off the field? Let's recap what happened.", "tool_link": "/", "icon": (<AssessmentIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></AssessmentIcon>)},
                                    {"tool_name": "View More", "tool_text": "Click to view all post-game tools", "tool_link": "/", "icon": (<UnfoldMoreDoubleIcon sx = {{"font-size": '6vh'}} className='tool-icon' ></UnfoldMoreDoubleIcon>)}
                                ]
                            }>
                        </ToolFolder>
                    </div>
                </div>
            </div>

        </div>
    );
}

export default Hub;